<template>
	<view class="content">
		<!-- 搜索框 -->
		<view class="search-area">
			<u-search v-model="params.keywords" @custom="search" @search="search" :shape="'round'" @clear="clear" 
				:clearabled="false" :show-action="false" :input-align="'left'" placeholder="热门话题"></u-search>
		</view>
		
		<!-- 类别筛选 -->
		<view class="u-tabs-box">
			<u-tabs :list="categoryList" @click="changeCategory" :current="categoryCurrent"></u-tabs>
		</view>
		
		<!-- 幻灯片栏 -->
		<view class="swiper-box">
			<u-swiper :list="bannerList" keyName="bannerUrl" :imgDomain="imgDomain" indicator indicatorMode="line" circular></u-swiper>
		</view>
		
		<view class="bbsbox" v-if="bbsList.length > 0">
			<view class="bbsview" v-for="(item,index) in bbsList" :key="index">
				<view class="mainview" @click="clickItem(item)">
					<view class="top">
						<view class="topleftimg">
							<u-avatar :src="item.avatar" size="40"></u-avatar>	
						</view>
						<view class="topleftname">
							<p>{{item.umame}}</p>
							<p>{{item.ctime}}</p>
						</view>			
					</view>
					<view class="content">
						<view>{{item.content}}</view>
						<view class="photo">
							<view class="photo-item" v-for="(imgItem, imgIndex) in item.imgUrlList" :key="imgIndex">
								<image :src="imgItem" mode="aspectFill"></image>
							</view>
						</view>
					</view>
				</view>			
				<view class="plview">
					<u-row gutter="16">
						<u-col span="4">
							<view class="plview-item" v-if="item.dz === 0">
								<u-icon name="thumb-up" color="#000" size="20"></u-icon>
								<text class="plview-item-text">点赞({{item.dzmun}})</text>
							</view>
							<view class="plview-item" v-else>
								<u-icon name="thumb-up-fill" color="#3776ff" size="20"></u-icon>
								<text class="plview-item-text active">已点赞({{item.dzmun}})</text>
							</view>
						</u-col>
						<u-col span="4">
							<view class="plview-item">
								<u-icon name="chat" color="#000" size="20"></u-icon>
								<text class="plview-item-text">评论({{item.plmun}})</text>
							</view>
						</u-col>
						<u-col span="4">
							<view class="plview-item" v-if="item.sc === 0">
								<u-icon name="star" color="#000" size="20"></u-icon>
								<text class="plview-item-text">收藏</text>
							</view>
							<view class="plview-item" v-else>
								<u-icon name="star-fill" color="#3776ff" size="20"></u-icon>
								<text class="plview-item-text active">已收藏</text>
							</view>
						</u-col>
					</u-row>
				</view>
				<view class="separate"></view>
			</view>
		</view>
		<!-- #ifdef H5 -->
		<view class="publish-area" style="bottom: 120rpx;">
		<!-- #endif -->
		<!-- #ifdef MP-WEIXIN -->
		<view class="publish-area">
		<!-- #endif -->
			<view class="publish-btn" @click="publishPost">
				<u-icon name="edit-pen-fill" color="#434343" size="30" 
					label="发布" labelPos="bottom" labelSize="12" labelColor="#434343" space="-4"></u-icon>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		components: {  },
		data() {
			return {
				//类别
				categoryList: [
					{ name: '最新' }, { name: '最热' }, { name: '精选' }
				],
				categoryCurrent: 0,
				// banner
				bannerList: [ ],
				//bbs
				bbsList:[
					{umame: 'jtom', avatar: require('@/static/img/user/default.png'), ctime: '2022-04-13 16:11', content: '新能源汽车安全吗?', dz: 0, dzmun: 10, plmun: 11, sc: 0,
						imgUrlList: [
							'http://124.223.108.78:8888/group1/M00/00/00/CgAEB2Jc26eAHODFAACN9klFFb884.jpeg', 
						]
					},
					{umame: 'jtom', avatar: require('@/static/img/user/default.png'), ctime: '2022-04-13 15:11', content: '新能源汽车前景如何?', dz: 1, dzmun: 10, plmun: 20, sc: 1,
						imgUrlList: [
							'http://124.223.108.78:8888/group1/M00/00/00/CgAEB2Jc3DiAdYyFAACqzOSmcmQ939.jpg', 
							'http://124.223.108.78:8888/group1/M00/00/00/CgAEB2Jc3ESAZxboAACN9klFFb866.jpeg'
						]
					}
				],
				//参数
				params:{ 
					pageNum: 1,
					pageSize: 5,
					bannerType: 3,
				},
				
				//图片域名
				imgDomain: this.$config.IMG_URL,
			}
		},
		onLoad() {
			this.loadData();
		},
		methods: {
			//加载数据
			async loadData(){
				//banner
				const bannerRes = await this.$http({url: '/wechat/banner/list', data: this.params});
				this.bannerList = bannerRes.rows;
			},
			//改变类别
			changeCategory(item){
				this.categoryCurrent = item.index;
			},
			//进入详情
			clickItem(){
				this.$u.route({ params: this.params, url: 'pages/bbs/detail' })
			},
			//发布帖子
			publishPost(){
				this.$u.route({ params: this.params, url: 'pages/bbs/publish' })
			},
			//搜索帖子
			search(){
				
			},
			clear() {
				this.params.keywords = '';
			},
		},
	}
</script>

<style lang="scss">
.search-area{
	background-color: #FFFFFF;
	padding: 0 20rpx 10rpx 20rpx;
}	

.swiper-box {
	width: 100%;
	height: auto;
	box-sizing: border-box;
	padding: 10rpx 30rpx;
	background-color: white;
}
	
.bbsbox{
	width: 100%;
	box-sizing: border-box;
	overflow-x:hidden;
	padding: 20rpx 0 0 30rpx;
	.bbsview{
		width: 100%;
		padding: 20rpx 0;
		.mainview{
			.top{
				display: flex;
			}
			.topleftimg{
				margin-right: 20rpx;
			}
			.topleftname{
				line-height: 30rpx;
				color: #999;
				font-size: 25rpx;
				p:first-child{
					color: #000;
					font-size: 22rpx;
					font-weight: bold;
					line-height: 50rpx;
					margin-top: -10rpx;
					margin-bottom: 10rpx;
				}
			}
			.content{
				width: 100%;
				margin: 30rpx 0 30rpx 0;
				text-overflow: ellipsis;
				text-overflow: -o-ellipsis-lastline;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 3;
				line-clamp: 3;
				-webkit-box-orient: vertical;
				.photo {
					display: flex;
					padding: 20rpx 0 0 0;
					image {
						height: 170rpx;
						width: 222rpx;
						border-radius: 10rpx;
					}
					.photo-item{
						margin: 0 10rpx 0 0;
					}
				}
			}
			.conimg{
				margin-top: 30rpx;
				margin-bottom: 10rpx;
			}
		}
		.plview{
			height: 70rpx;
			.plview-item{
				display: flex;
				.plview-item-text{
					font-size: 26rpx;
					padding: 0 0 0 10rpx;
				}
			}
		}
		.separate{
			width: 95%;
			background-color: #f9f9f9;
			height: 10rpx;
		}
	}
}
.active{
	color: #3776ff;
}
.publish-area{
	position: fixed; 
	bottom: 20rpx;
	right: 20rpx;
	display: flex;
	.publish-btn{
		width: 90rpx;
		height: 90rpx;
		background-color:#ffd554;
		border-radius: 50%;
		-moz-border-radius: 50%;
		-webkit-border-radius: 50%;
		display: inline-grid;
		justify-content: center;
		text-align: center;
		text{
			font-size: 25rpx;
			margin-top: -6rpx;
		}
	}
}
</style>
